.about__container {
    display: grid;
    grid-template-columns: 35% 53%;
    gap: 12%;
    align-items: center;
    max-width: 100%; 
    overflow: hidden;
}

.about__portrait {
    max-width: 100%;
    height: fit-content;
    max-height: 40rem;
    border: 1rem solid var(--color-white);
    overflow: hidden; 
    transition: var(--transition);
    display: flex;
    justify-content: center;
    align-items: center;
}

.about__portrait img {
    max-width: 100%; 
    max-height: 100%; 
    width: auto; 
    height: auto;
    object-fit: cover;
    border-radius: 0.5rem; 
    transition: var(--transition);
}
/* .about__portrait:hover{
    border-width: 0;
} */

.about__cards{
    margin: 3rem 0 1.5rem;
    display: grid;
    grid-template-columns: repeat(3 , 1fr);
    gap: 1.5rem;
    cursor: pointer;
}

.card.about__card{
    text-align: center;
}

.about__card-icon{
    font-size: 1.3rem;
    color: var(--color-black);
    border-radius: var(--border-radius-3);
    width: fit-content;
    margin-inline: auto;
    padding: 0.6rem;
    display: flex;
    align-items: center;
    transition: var(--transition);
}

.card.about__card:hover .about__card-icon{
    background: var(--color-primary);
    color : var(--color-white);
}

.card.about__card h5{
    margin: 1rem 0;
}

.about__right p{
    margin-bottom: 0.8rem;
}

.about__right .btn{
    margin-top: 2.5rem;
}

@media screen and (max-width : 1024px) {
    .about__left {
        display: none;
    }
    .about__container{
        grid-template-columns: 1fr;
    }
    .about__right h2{
        text-align: center;
    }
}

@media screen and (max-width : 600px) {
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__right .btn{
        margin-top: 2rem;
    }
}