#floating__nav {
    display: none;
  }
  
  @media screen and (max-width: 1024px) {
    #floating__nav {
      display: flex;
      align-items: center;
      position: fixed;
      bottom: 3rem;
      left: 50%;
      transform: translateX(-50%);
      z-index: 9;
    }
    .scrollspy {
      display: flex;
      gap: 1rem;
      background: rgba(0, 0, 0, 0.5);
      padding: 0.8rem 1.5rem;
      border-radius: 3rem;
      backdrop-filter: blur(20px);
    }
  
    #floating__nav a {
      padding: 0.6rem;
      border-radius: 50%;
      font-size: 1.3rem;
      color: white;
      display: grid;
      place-items: center;
    }
  
    #floating__nav li.active a {
      background: var(--color-primary);
    }
  }
  